'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import PairingForm from '@/components/device-control/PairingForm'

interface Device {
  id: string
  name: string
  ip: string
  status: 'connected' | 'disconnected' | 'connecting'
  lastSeen: string
}

export default function HomePage() {
  const router = useRouter()
  const [connectedDevices, setConnectedDevices] = useState<Device[]>([])

  const handleDeviceConnect = (device: Device) => {
    setConnectedDevices(prev => [...prev, device])
  }

  const handleDeviceDisconnect = (deviceId: string) => {
    setConnectedDevices(prev => prev.filter(device => device.id !== deviceId))
  }

  const handleDeviceControl = (deviceId: string) => {
    router.push(`/device/${deviceId}`)
  }

  return (
    <div className="container mx-auto p-6 max-w-6xl">
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-2">AirDroid Clone</h1>
        <p className="text-muted-foreground text-lg">
          Telefonunuzu bilgisayarınızdan güvenli şekilde kontrol edin
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Cihaz Bağlantı Paneli */}
        <div className="lg:col-span-1">
          <PairingForm
            onDeviceConnect={handleDeviceConnect}
            connectedDevices={connectedDevices}
            onDeviceDisconnect={handleDeviceDisconnect}
          />
        </div>

        {/* Ana Kontrol Paneli */}
        <div className="lg:col-span-2">
          {connectedDevices.length > 0 ? (
            <Card>
              <CardHeader>
                <CardTitle>Bağlı Cihazlar</CardTitle>
                <CardDescription>
                  Kontrol etmek istediğiniz cihazı seçin
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {connectedDevices.map((device) => (
                  <div key={device.id} className="p-4 border rounded-lg">
                    <div className="flex items-center justify-between mb-3">
                      <div>
                        <h3 className="font-semibold">{device.name}</h3>
                        <p className="text-sm text-muted-foreground">{device.ip}</p>
                        <p className="text-xs text-muted-foreground">Son görülme: {device.lastSeen}</p>
                      </div>
                      <Badge variant={device.status === 'connected' ? 'default' : 'secondary'}>
                        {device.status === 'connected' ? 'Bağlı' : 'Bağlantı Kesildi'}
                      </Badge>
                    </div>
                    
                    <div className="flex gap-2">
                      <Button 
                        onClick={() => handleDeviceControl(device.id)}
                        disabled={device.status !== 'connected'}
                        className="flex-1"
                      >
                        Cihazı Kontrol Et
                      </Button>
                      <Button
                        variant="outline"
                        onClick={() => handleDeviceDisconnect(device.id)}
                      >
                        Bağlantıyı Kes
                      </Button>
                    </div>
                  </div>
                ))}
              </CardContent>
            </Card>
          ) : (
            <Card>
              <CardContent className="p-8 text-center">
                <h3 className="text-xl font-medium mb-2">Cihaz Bağlantısı Bekleniyor</h3>
                <p className="text-muted-foreground">
                  Kontrol panelini kullanmak için önce bir Android cihazı bağlayın
                </p>
              </CardContent>
            </Card>
          )}
        </div>
      </div>

      {/* Özellikler Kartları */}
      <div className="mt-8">
        <h2 className="text-2xl font-bold mb-4">Özellikler</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-blue-600 rounded"></div>
              </div>
              <h3 className="font-medium mb-2">Uzaktan Ekran Kontrolü</h3>
              <p className="text-sm text-muted-foreground">
                Cihazınızın ekranını canlı izleyin ve dokunmatik kontrol edin
              </p>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-green-600 rounded-full"></div>
              </div>
              <h3 className="font-medium mb-2">Kamera Erişimi</h3>
              <p className="text-sm text-muted-foreground">
                Ön ve arka kamerayı kontrol edin, fotoğraf çekin
              </p>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-purple-600 rounded-lg"></div>
              </div>
              <h3 className="font-medium mb-2">SMS ve Kişiler</h3>
              <p className="text-sm text-muted-foreground">
                Mesajları yönetin ve rehber erişimi
              </p>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-red-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-red-600 rounded-full"></div>
              </div>
              <h3 className="font-medium mb-2">Konum Takibi</h3>
              <p className="text-sm text-muted-foreground">
                GPS konumunu takip edin ve geçmişi görüntüleyin
              </p>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-yellow-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-yellow-600 rounded"></div>
              </div>
              <h3 className="font-medium mb-2">Galeri Yönetimi</h3>
              <p className="text-sm text-muted-foreground">
                Fotoğraf ve videoları görüntüleyin, indirin
              </p>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-orange-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-orange-600 rounded-lg"></div>
              </div>
              <h3 className="font-medium mb-2">Dosya Aktarımı</h3>
              <p className="text-sm text-muted-foreground">
                Dosyaları güvenli şekilde aktarın
              </p>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-gray-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-gray-600 rounded-full"></div>
              </div>
              <h3 className="font-medium mb-2">Cihaz Sıfırlama</h3>
              <p className="text-sm text-muted-foreground">
                Güvenli cihaz temizleme ve sıfırlama
              </p>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="w-12 h-12 bg-indigo-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                <div className="w-6 h-6 bg-indigo-600 rounded"></div>
              </div>
              <h3 className="font-medium mb-2">Güvenli Bağlantı</h3>
              <p className="text-sm text-muted-foreground">
                Token tabanlı şifreli erişim
              </p>
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Kurulum Talimatları */}
      <div className="mt-8">
        <Card>
          <CardHeader>
            <CardTitle>Kurulum Talimatları</CardTitle>
            <CardDescription>
              Android uygulamasını kurmak ve bağlantı sağlamak için aşağıdaki adımları izleyin
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-3">
                <h4 className="font-medium">Android Uygulaması</h4>
                <div className="space-y-2 text-sm">
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">1</Badge>
                    <p>Android Studio'da AirDroid Clone projesini açın</p>
                  </div>
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">2</Badge>
                    <p>Uygulamayı cihazınıza yükleyin (APK olarak)</p>
                  </div>
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">3</Badge>
                    <p>Gerekli izinleri verin (Kamera, Konum, SMS, vb.)</p>
                  </div>
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">4</Badge>
                    <p>Uygulamayı açın ve IP adresini not edin</p>
                  </div>
                </div>
              </div>
              
              <div className="space-y-3">
                <h4 className="font-medium">Web Panel Bağlantısı</h4>
                <div className="space-y-2 text-sm">
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">1</Badge>
                    <p>Android cihazınızın IP adresini kopyalayın</p>
                  </div>
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">2</Badge>
                    <p>Sol paneldeki forma IP adresini girin</p>
                  </div>
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">3</Badge>
                    <p>"Cihaza Bağlan" butonuna tıklayın</p>
                  </div>
                  <div className="flex items-start gap-2">
                    <Badge variant="outline">4</Badge>
                    <p>Bağlantı kurulduktan sonra "Cihazı Kontrol Et" butonunu kullanın</p>
                  </div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
