'use client'

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'

interface Device {
  id: string
  name: string
  ip: string
  status: 'connected' | 'disconnected' | 'connecting'
  lastSeen: string
}

interface PairingFormProps {
  onDeviceConnect: (device: Device) => void
  connectedDevices: Device[]
  onDeviceDisconnect: (deviceId: string) => void
}

export default function PairingForm({ 
  onDeviceConnect, 
  connectedDevices, 
  onDeviceDisconnect 
}: PairingFormProps) {
  const [deviceIp, setDeviceIp] = useState('')
  const [deviceName, setDeviceName] = useState('')
  const [isConnecting, setIsConnecting] = useState(false)
  const [error, setError] = useState('')

  const validateIp = (ip: string): boolean => {
    const ipRegex = /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/
    return ipRegex.test(ip)
  }

  const handleConnect = async () => {
    setError('')
    
    if (!deviceIp.trim()) {
      setError('IP adresi gereklidir')
      return
    }
    
    if (!validateIp(deviceIp)) {
      setError('Geçerli bir IP adresi girin')
      return
    }

    if (connectedDevices.some(device => device.ip === deviceIp)) {
      setError('Bu IP adresinden zaten bağlı bir cihaz var')
      return
    }

    setIsConnecting(true)

    try {
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      const newDevice: Device = {
        id: Date.now().toString(),
        name: deviceName.trim() || 'Android Cihaz',
        ip: deviceIp,
        status: 'connected',
        lastSeen: new Date().toLocaleString('tr-TR')
      }
      
      onDeviceConnect(newDevice)
      setDeviceIp('')
      setDeviceName('')
    } catch (err) {
      setError('Bağlantı kurulamadı')
    } finally {
      setIsConnecting(false)
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Cihaz Eşleştirme</CardTitle>
        <CardDescription>
          Android cihazınızın IP adresini girerek güvenli bağlantı kurun
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        <div className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="device-name">Cihaz Adı</Label>
            <Input
              id="device-name"
              placeholder="Benim Telefonum"
              value={deviceName}
              onChange={(e) => setDeviceName(e.target.value)}
              disabled={isConnecting}
            />
          </div>
          
          <div className="space-y-2">
            <Label htmlFor="device-ip">IP Adresi</Label>
            <Input
              id="device-ip"
              placeholder="192.168.1.100"
              value={deviceIp}
              onChange={(e) => setDeviceIp(e.target.value)}
              disabled={isConnecting}
            />
          </div>

          {error && (
            <Alert>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <Button 
            onClick={handleConnect}
            disabled={isConnecting || !deviceIp.trim()}
            className="w-full"
          >
            {isConnecting ? 'Bağlanıyor...' : 'Cihaza Bağlan'}
          </Button>
        </div>

        <div className="space-y-3">
          <h4 className="font-medium">Bağlı Cihazlar</h4>
          {connectedDevices.length === 0 ? (
            <p className="text-sm text-muted-foreground">
              Henüz bağlı cihaz yok
            </p>
          ) : (
            <div className="space-y-2">
              {connectedDevices.map((device) => (
                <div key={device.id} className="flex items-center justify-between p-3 border rounded-lg">
                  <div className="flex-1">
                    <p className="font-medium text-sm">{device.name}</p>
                    <p className="text-xs text-muted-foreground">{device.ip}</p>
                    <p className="text-xs text-muted-foreground">Son görülme: {device.lastSeen}</p>
                  </div>
                  <div className="flex items-center gap-2">
                    <Badge variant={device.status === 'connected' ? 'default' : 'secondary'}>
                      {device.status === 'connected' ? 'Bağlı' : 'Bağlantı Kesildi'}
                    </Badge>
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => onDeviceDisconnect(device.id)}
                    >
                      Kes
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  )
}
